import { FC, useRef, useState } from "react"
import 'slick-carousel/slick/slick.css'
import 'slick-carousel/slick/slick-theme.css'
import { IoIosArrowRoundForward } from "react-icons/io";
import { IoIosArrowRoundBack } from "react-icons/io";
import CardSignals from "./CardSignal";
import useEmblaCarousel from 'embla-carousel-react'
import { EmblaOptionsType } from 'embla-carousel'
import { PrevButton, NextButton, usePrevNextButtons } from './EmblaCarouselArrowButtons';


const OPTIONS: EmblaOptionsType = { slidesToScroll: 'auto' }


const SignalsCarousel = () => {
    const [emblaRef, emblaApi] = useEmblaCarousel(OPTIONS)
    const signals = [
        {
            par: 'AUD-JPY',
            name_user: 'Luciano',
            id: '1',
            created_at: new Date(),
            order_type: 'sell'
        },
        {
            par: 'US30',
            name_user: 'Luciano',
            id: '1',
            created_at: new Date(),
            order_type: 'buy'
        }
    ]

    const {
        prevBtnDisabled,
        nextBtnDisabled,
        onPrevButtonClick,
        onNextButtonClick
      } = usePrevNextButtons(emblaApi)
   

    if (signals.length === 0) return (
        <span className="text-black mt-4 text-3xl font-bold leading-18 fade-text">
            Parece que todavía no tienes señales
        </span>
    )

    return (
        <div className="flex flex-col gap-8 p-8 md:px-8 xl:px-14">
            <div className="hidden mt-4 md:mt-0 md:flex z-[2]">
                <div className="flex flex-col items-left ">
                    <span className=" text-[48px] font-bold leading-18 p-2 md:p-0 truncate fade-text">
                        Últimas señales
                    </span>
                </div>
            </div>
            <div className="relative w-full bg-white rounded-3xl">
                <div className="relative ">
                    <section className="embla">
                        <div className="embla__viewport" ref={emblaRef}>
                            <div className="embla__container">
                                {signals.map((par: any, index) => (
                                    <div className="embla__slide" key={index}>
                                        <div className="embla__slide__number">
                                            <CardSignals par={par} key={index} />
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>

                        <div
                            className="absolute top-16 lg:top-16 left-2 cursor-pointer flex items-center justify-center text-center  p-1"
                        >
                            <PrevButton onClick={onPrevButtonClick} disabled={prevBtnDisabled} />

                        </div>
                        <div
                            className="absolute top-16 lg:top-16 right-2 cursor-pointer flex items-center justify-center  p-1"

                        >
                            <NextButton onClick={onNextButtonClick} disabled={nextBtnDisabled} />
                        </div>
                    </section>
                   
                </div>
            </div>
        </div>
    )
}
export default SignalsCarousel

