import classNames from "classnames"
import { FC } from "react"

type Props = {
  par: any
}

export const types:any = {
  sell_limit: "Sell Limit",
  buy_limit: "Buy Limit",
  sell_stop: "Sell Stop",
  buy_stop: "Buy Stop",
  sell: "Sell",
  buy: "Buy"
}

// deploy

const CardSignals: FC<Props> = ({ par }) => {
  return (
    <a href={`/signals/${par.id}`}>
      <div className="px-2">
        <div className="!flex !flex-col relative bg-white p-4 px-8 rounded-lg gap-4 h-full">
          <div className="!flex !flex-col !lg:flex-row !items-center !justify-center gap-4 ">
            <div className="rounded-full">
              <img
                src={`img/PARES/${par.par}.svg`}
                className="w-[80px] h-[80px] object-cover rounded-full aspect-square"
                alt=""
              />
            </div>
            <div className="flex flex-col gap-1">
              <span className="!text-xl font-bold text-black leading-[8px] whitespace-nowrap">
                {par.par.replace('-', '')}
              </span>
              <span className={classNames("!text-[15px]", par.order_type.includes("sell") && "text-red-500", par.order_type.includes("buy") && "text-green-500")}>{types[par.order_type as any] || ""}</span>
            </div>
          </div>
        </div>
      </div>
    </a>
  )
}
export default CardSignals