/* eslint-disable @next/next/no-img-element */
import { useState, ChangeEvent, FormEvent, FC } from "react";
import {
  Form,
  Input,
  Button,
  Spacer,
  Select,
  SelectItem,
} from "@nextui-org/react";
import { FormErrors, FormSignals } from "@/types/Forms";
import { toast } from "react-hot-toast";
import { pares, orderTypes } from "@/constants";
import axios from "axios";

type Props = {
  onClose: () => void;
};

const FormSignal: FC<Props> = ({ onClose }) => {
  const [values, setValues] = useState<FormSignals>({
    par: "",
    order_type: "",
    entry_price: "",
    stop_loss: "",
    take_profit_1: "",
    take_profit_2: "",
    take_profit_3: "",
    img_analisis: null,
    notes: "",
  });

  const [errors, setErrors] = useState<FormErrors>({});
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [selectedPar, setSelectedPar] = useState<string | null>("");

  const validate = (): boolean => {
    const newErrors: FormErrors = {};

    if (!values.par) newErrors.par = "Por favor introduzca el par";
    if (!values.order_type)
      newErrors.order_type = "Por favor introduzca el tipo de orden";
    if (!values.entry_price)
      newErrors.entry_price = "Por favor introduzca el precio de entrada";
    if (!values.stop_loss)
      newErrors.stop_loss = "Por favor introduzca el precio de salida";
    if (!values.take_profit_1)
      newErrors.take_profit_1 = "Por favor introduzca la ganancia estimada";
    if (!values.img_analisis)
      newErrors.img_analisis = "Por favor suba el análisis";

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleChange = (
    e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setValues((prev) => ({ ...prev, [name]: value }));
    setErrors((prev) => ({ ...prev, [name]: "" }));
  };

  /*const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0] || null;
    setValues((prev) => ({ ...prev, img_analisis: file }));
    setErrors((prev) => ({ ...prev, img_analisis: "" }));
  };*/

  const handleSelectChange = (name: string, value: string) => {
    setValues((prev) => ({ ...prev, [name]: value }));
    setErrors((prev) => ({ ...prev, [name]: "" }));
  };

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (validate()) {
      setIsSubmitting(true);
      try {
        const payload = { ...values };
        const res = await axios.post(
          `${process.env.NEXT_PUBLIC_URL_API}/signals/add-signal`,
          payload
        );

        if (res.status === 200) {
          toast.success("Señal agregada con éxito");
          setValues({
            par: "",
            order_type: "",
            entry_price: "",
            stop_loss: "",
            take_profit_1: "",
            take_profit_2: "",
            take_profit_3: "",
            img_analisis: null,
            notes: "",
          });
        } else {
          toast.error("No se pudo agregar la señal");
        }
      } catch (error) {
        console.error("Error al agregar la señal:", error);
        toast.error("No se pudo agregar la señal");
      } finally {
        setIsSubmitting(false);
      }
    }
  };

  return (
    <div className="border p-4 relative rounded-lg border-[#3f3f3f]">
      <Form onSubmit={handleSubmit}>
        <span>Detalles de la señal</span>
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-4 w-full">
          <Select
            label="Par"
            name="par"
            value={values.par}
            onChange={(value) => {
              handleSelectChange("par", value.target.value);
              setSelectedPar(value.target.value || "");
            }}
            errorMessage={errors.par}
            isInvalid={!!errors.par}
          >
            {pares.map((par) => (
              <SelectItem key={par} value={par}>
                {par.replace("-", "")}
              </SelectItem>
            ))}
          </Select>

          {selectedPar && (
            <img
              src={`/img/PARES/${selectedPar}.svg`}
              alt={`Imagen del par ${selectedPar}`}
              className="w-24 h-24 mt-4"
            />
          )}

          <Select
            label="Tipo de Orden"
            name="order_type"
            value={values.order_type}
            onChange={(value) =>
              handleSelectChange("order_type", value.target.value)
            }
            errorMessage={errors.order_type}
            isInvalid={!!errors.order_type}
          >
            {orderTypes.map((order) => (
              <SelectItem key={order.value} value={order.value}>
                {order.label}
              </SelectItem>
            ))}
          </Select>

          <Input
            label="Precio de Entrada"
            name="entry_price"
            type="number"
            value={values.entry_price}
            onChange={handleChange}
            errorMessage={errors.entry_price}
            isInvalid={!!errors.entry_price}
          />

          <Input
            label="Stop Loss"
            name="stop_loss"
            type="number"
            value={values.stop_loss}
            onChange={handleChange}
            errorMessage={errors.stop_loss}
            isInvalid={!!errors.stop_loss}
          />

          <Input
            label="Take Profit 1"
            name="take_profit_1"
            type="number"
            value={values.take_profit_1}
            onChange={handleChange}
            errorMessage={errors.take_profit_1}
            isInvalid={!!errors.take_profit_1}
          />

          {/*<Input
            label="Archivo de Análisis"
            type="file"
            name="img_analisis"
            onChange={handleFileChange}
            errorMessage={errors.img_analisis}
            isInvalid={!!errors.img_analisis}
          />*/}

          <Input
            label="Notas"
            name="notes"
            type="text"
            value={values.notes}
            onChange={handleChange}
          />
        </div>

        <Spacer y={1} />
        <Button type="submit" disabled={isSubmitting}>
          {isSubmitting ? "Enviando..." : "Agregar Señal"}
        </Button>
      </Form>
    </div>
  );
};

export default FormSignal;
